import React from 'react';
import Nav from './Nav'
import LoginForm from './LoginForm'
import SignupForm from './SignupForm'

//const BASE_URL = 'http://localhost:8000/';
const BASE_URL = 'https://pt-alpha-final-project.herokuapp.com/';

class LoginSignUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      displayed_form: '',
      logged_in: localStorage.getItem('token') ? true : false,
      username: '',
      invalid_credentials_warning: false
    };
  }

  componentDidMount() {
    if (this.state.logged_in) {
      fetch(`${BASE_URL}/covid/current_user/`, {
        headers: {
          Authorization: `JWT ${localStorage.getItem('token')}`
        }
      })
        .then(res => res.json())
        .then(json => {
          this.setState({ username: json.username });
        });
    }
  }

  handle_login = (e, data) => {
    e.preventDefault();
    fetch(`${BASE_URL}token-auth/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
    .then(res => {
      if (res['status'] >= 400) {
        this.setState({
          logged_in: false,
          invalid_credentials_warning: true
        });
      } 
      else {
        const json = res.json()
        localStorage.setItem('token', json.token);
        this.setState({
          logged_in: true,
          displayed_form: '',
          username: json.user.username,
        });
        localStorage.setItem('username', json.user.username)
      }
    })
  }

  handle_signup = (e, data) => {
    e.preventDefault();
    fetch(`${BASE_URL}covid/users/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
      .then(res => res.json())
      .then(json => {
        localStorage.setItem('token', json.token);
        this.setState({
          logged_in: true,
          displayed_form: '',
          username: json.username
        });
      });
  };

  handle_logout = () => {
    localStorage.removeItem('token');
    this.setState({ logged_in: false, username: '' });
  };

  display_form = form => {
    this.setState({
      displayed_form: form
    });
  };

  render() {

    let form;
    switch (this.state.displayed_form) {
      case 'login':
        form = <LoginForm handle_login={this.handle_login} invalid_credentials_warning={this.state.invalid_credentials_warning}/>;
        break;
      case 'signup':
        form = <SignupForm handle_signup={this.handle_signup} />;
        break;
      default:
        form = null;
    }
    return (
      <div>

        <Nav
          logged_in={this.state.logged_in}
          display_form={this.display_form}
          handle_logout={this.handle_logout}
        />
        {form}
        <h3>
          {this.state.logged_in
            ? `Hello, ${this.state.username}`
            : ''}
        </h3>

      </div>
    )
  }

}




export default LoginSignUp;
